import create from 'zustand';
import * as locationAPI from 'lib/api/location';
import { persist } from 'zustand/middleware';

export const memLocStore = create(
  persist(
    (set, get) => ({
      // memberLocation
      memberLocations: [],

      initializeMemberForm: () => {
        set(() => ({
          memberLocations: [],
        }));
      },

      // get MemberLocations
      getMemberLocations: async () => {
        const res = await locationAPI.getMemberLocation();
        if (!res) return;
        let memLoc = [];
        Object.keys(res.data.blockLocations).forEach((type, i) => {
          res.data.blockLocations[type].forEach((loc) => {
            memLoc.push(loc);
          });
        });
        set(() => ({
          memberLocations: memLoc,
        }));
        return memLoc;
      },

      // memLoc  
      createMemberLoc: async (main, coords, sub, typeObj, type) => {
        const { name, share, address1, summary } = main;
        const { latitude, longitude } = coords;
        const { report, address2, image, image1, image2, tel } = sub;

        if (name === undefined) return '  .';
        else if (address1 === undefined) return '  .';
        else if (share === undefined) return '   .';
        else if (summary === undefined)
          return '    .';
        //   //   
        let isShare = false;
        if (share === 'true') isShare = true;
        let loc = {
          memberLocation: {
            memberId: 1, //     ..?
            isPublic: isShare,
          },
          location: {
            address1,
            address2,
            coords: {
              latitude,
              longitude,
            },
            image,
            name,
            isMember: true,
            areaCode: 39, // ?
            type: {
              type,
            },
          },
          typeLocation: typeObj,
          information: {
            image1,
            image2,
            report,
            summary,
            tel,
          },
        };
        const res = await locationAPI.createMemberLocation(loc);
        if (res.status === 201) {
          let smallLoc = loc.location;
          smallLoc.locationId = res.data;
          set((state) => ({
            memberLocations: [...state.memberLocations, smallLoc],
          }));
          return 'success';
        } else {
          return '  !!';
        }
      },

      //      
      typeInfo: {
        Attraction: {
          parking: [' ', false],
          restDate: ['', 'ex.   '], // null
          useTime: [' ', 'ex. 09:00 ~ 22:00'], // 09:00 ~ 22:00 ( 21:20)
        },
        Culture: {
          parking: [' ', false],
          restDate: ['', 'ex.   '],
          fee: ['', '10,000'],
          useTime: [' ', 'ex. 09:00 ~ 22:00'],
          spendTime: ['', 'ex. 2'],
        },
        Festival: {
          startDate: ['', '20220930'], // 20220318
          endDate: ['', '20221009'], // 20220320
          homepage: [
            ' ',
            'http://www.maskdance.com/2019/sub7/sub0.asp',
          ], // null
          place: ['', '   239 '], //    ()
          placeInfo: [' ?', '  '],
          playTime: [' ', '30'],
          program: ['', ''],
          fee: [' ', ' or 10000'],
        },
        Leports: {
          parking: [' ', false],
          openPeriod: [' ', ''],
          reservation: ['', ' ?'],
          restDate: ['', 'ex.   '],
          fee: [' ', ' or 10000'],
          useTime: [' ', 'ex. 09:00 ~ 22:00'],
        },
        Lodge: {
          checkInTime: ['', '18:00'],
          checkOutTime: ['', '12:00'],
          cooking: [' ', false],
          parking: [' ', false],
          reservationUrl: [' ', 'www.example.com'],
          subfacility: [' ', ''],
        },
        Restaurant: {
          popularMenu: ['', ', ...'],
          openTime: [' ', 'ex. 09:00 ~ 22:00'],
          packing: [' ', false],
          parking: [' ', false],
          restDate: ['', 'ex.   '],
          menu: ['', ': 3000, : 4000, ...'],
        },
      },
    }),
    {
      //    , sessionStorage 
      name: 'memberLoc-storage',
      getStorage: () => sessionStorage,
    },
  ),
);
